<template>
  <div class="accountrecord">
    <j-header title="收支明细"></j-header>
    <main ref="container">
      <ul v-for="(obj, i) in list" :key="i">
        <li>
          <span>时间：{{ obj.createtime }}</span>
          <span :style="{ color: status[obj.status] }">{{ obj.status || '无' }}</span>
        </li>
        <li>
          <span>类型：{{ obj.type }}</span>
          <span>订单号：{{ obj.order_no }}</span>
        </li>
        <li>
          <span>金额：{{ obj.amount }}</span>
          <span>手续费：0.00</span>
        </li>
        <li>
          <span>备注：{{ obj.remark || '无' }}</span>
          <svg-icon :icon-class="icons[obj.status]"></svg-icon>
        </li>
      </ul>
    </main>
    <j-nothing
      :show="list.length < 1"
      :url="require('@/assets/inquiries/nothing.png')"
      title="您还没有收支数据哦~"
      router="homepage"></j-nothing>
  </div>
</template>

<script>
// @ is an alias to /src
import JHeader from '@/components/JHeader'
import JNothing from '@/components/JNothing'
import { scroll } from '@/utils'

export default {
  name: 'accountrecord',
  components: {
    JHeader,
    JNothing
  },
  data () {
    return {
      list: [],
      status: {
        '审核中': '#ef5717',
        '成功': '#09bb07',
        '失败': '#f60000',
        '': '#f60000'
      },
      icons: {
        '审核中': 'audit',
        '成功': 'success',
        '失败': 'fail',
        '': 'fail'
      }
    }
  },
  created () {
    this.init()
  },
  methods: {
    init (obj) {
      this.request({
        url: '/account',
        method: 'get',
        headers: obj || { 'X-Pagination-Per-Page': 10 },
        loading: true
      }).then(res => {
        res.code === 1 && (this.list = res.data)
      })
    }
  },
  mounted () {
    const container = this.$refs.container
    scroll(container, page => {
      this.init({ 'X-Pagination-Per-Page': page * 10 })
    }, scrollTop => {
      this.scrollTop = scrollTop
    })
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
